html {
    position: relative;
    min-height: 100%;
}
body {
    margin-bottom: 60px;    
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: #312d2a;
}
.container {
    width: auto;
    max-width: 680px;
    padding: 0 15px;
}
.container .text-muted {
    margin: 20px 0;
    color: var(--main-gray)!important;
}